/* =========================
   Zod Schemas
   ========================= */

const addSchema = z.object({
  title: z.string().min(2, "Enter a service name."),
  category: z.enum(
    CATEGORIES.filter((c) => c !== "All") as [
      Exclude<(typeof CATEGORIES)[number], "All">,
      ...string[]
    ],
    { required_error: "Select a category." }
  ),
  durationMin: z
    .preprocess((v) => Number(v), z.number().int().positive().max(1440))
    .refine((n) => Number.isFinite(n), "Enter a valid number."),
  price: z
    .preprocess((v) => Number(v), z.number().positive().max(100000))
    .refine((n) => Number.isFinite(n), "Enter a valid price."),
  image: z.url("Must be a valid URL").or(z.literal("")).optional(),
});
type AddValues = z.infer<typeof addSchema>;

const editSchema = addSchema;
type EditValues = z.infer<typeof editSchema>;

/* =========================
   Local, per-section slider
   ========================= */

function LocalHSlider({
  children,
  ariaLabel,
  step = 360,
  className,
}: {
  children: React.ReactNode;
  ariaLabel: string;
  step?: number;
  className?: string;
}) {
  const ref = React.useRef<HTMLDivElement>(null);
  const [canLeft, setCanLeft] = React.useState(false);
  const [canRight, setCanRight] = React.useState(false);

  const update = React.useCallback(() => {
    const el = ref.current;
    if (!el) return;
    setCanLeft(el.scrollLeft > 0);
    setCanRight(el.scrollLeft + el.clientWidth < el.scrollWidth - 1);
  }, []);

  React.useEffect(() => {
    update();
    const el = ref.current;
    if (!el) return;
    const onResize = () => update();
    window.addEventListener("resize", onResize);
    el.addEventListener("scroll", update, { passive: true });
    return () => {
      window.removeEventListener("resize", onResize);
      el.removeEventListener("scroll", update as any);
    };
  }, [update]);

  const onWheel: React.WheelEventHandler<HTMLDivElement> = (e) => {
    const el = ref.current;
    if (!el) return;
    if (Math.abs(e.deltaY) > Math.abs(e.deltaX)) {
      el.scrollBy({ left: e.deltaY, behavior: "auto" });
      e.preventDefault();
      e.stopPropagation();
    }
  };

  return (
    <div className={cn("relative", className)}>
      {/* gradient edges */}
      <div className="pointer-events-none absolute inset-y-0 left-0 w-12 bg-gradient-to-r from-background-light/80 dark:from-background-dark/80 to-transparent rounded-l-xl" />
      <div className="pointer-events-none absolute inset-y-0 right-0 w-12 bg-gradient-to-l from-background-light/80 dark:from-background-dark/80 to-transparent rounded-r-xl" />
      {/* arrows */}
      <div className="absolute inset-y-0 left-1 flex items-center">
        <Button
          type="button"
          size="icon"
          variant="secondary"
          className="h-8 w-8 rounded-full bg-primary/10"
          disabled={!canLeft}
          onClick={() =>
            ref.current?.scrollBy({ left: -step, behavior: "smooth" })
          }
          aria-label="Scroll left"
        >
          ‹
        </Button>
      </div>
      <div className="absolute inset-y-0 right-1 flex items-center">
        <Button
          type="button"
          size="icon"
          variant="secondary"
          className="h-8 w-8 rounded-full bg-primary/10"
          disabled={!canRight}
          onClick={() =>
            ref.current?.scrollBy({ left: step, behavior: "smooth" })
          }
          aria-label="Scroll right"
        >
          ›
        </Button>
      </div>
      {/* track */}
      <div
        ref={ref}
        role="region"
        aria-label={ariaLabel}
        onWheel={onWheel}
        className={cn(
          "overflow-x-auto overflow-y-hidden scroll-smooth rounded-xl border",
          "border-border-light dark:border-border-dark bg-content-light dark:bg-content-dark",
          "px-2",
          "snap-x snap-mandatory",
          "[-ms-overflow-style:'none'] [scrollbar-width:'none'] [&::-webkit-scrollbar]:hidden"
        )}
        tabIndex={0}
      >
        <div className="flex w-max gap-3 py-3">{children}</div>
      </div>
    </div>
  );
}

/* =========================
   Small UI bits
   ========================= */

function CategoryPill({
  active,
  label,
  onClick,
}: {
  active?: boolean;
  label: string;
  onClick?: () => void;
}) {
  return (
    <button
      type="button"
      onClick={onClick}
      className={cn(
        "shrink-0 snap-start rounded-full border px-4 py-2 text-sm transition",
        active
          ? "bg-primary text-white border-transparent"
          : "border-border-light dark:border-border-dark text-text-light dark:text-text-dark hover:bg-primary/10"
      )}
    >
      {label}
    </button>
  );
}

function FeaturedCard({
  item,
  onEdit,
  onDelete,
}: {
  item: ServiceRow;
  onEdit?: () => void;
  onDelete?: () => void;
}) {
  return (
    <div className="w-[260px] sm:w-[300px] lg:w-[340px] shrink-0 snap-start rounded-xl border border-border-light dark:border-border-dark bg-content-light dark:bg-content-dark overflow-hidden">
      <div
        className="aspect-video w-full bg-cover bg-center"
        style={{ backgroundImage: `url(${item.image ?? ""})` }}
        aria-label={`${item.title} image`}
      />
      <div className="p-4 flex flex-col gap-2">
        <div className="flex items-start justify-between gap-3">
          <div className="min-w-0">
            <p className="text-text-light dark:text-text-dark font-semibold truncate">
              {item.title}
            </p>
            <p className="text-subtext-light dark:text-subtext-dark text-sm">
              {item.cat} • {item.dur}
            </p>
          </div>
          <span className="text-text-light dark:text-text-dark text-sm font-medium">
            {item.price}
          </span>
        </div>
        <div className="flex gap-2 pt-1">
          <Button
            type="button"
            variant="secondary"
            className="h-8 px-3"
            onClick={onEdit}
          >
            Edit
          </Button>
          <Button
            type="button"
            variant="destructive"
            className="h-8 px-3"
            onClick={onDelete}
          >
            Delete
          </Button>
        </div>
      </div>
    </div>
  );
}

/* =========================
   Page
   ========================= */

export default function ManageServicesPage() {
  const [category, setCategory] =
    React.useState<(typeof CATEGORIES)[number]>("All");
  const [search, setSearch] = React.useState("");

  const [featured, setFeatured] = React.useState<ServiceRow[]>(
    () => INITIAL_FEATURED
  );
  const [rows, setRows] = React.useState<ServiceRow[]>(() => INITIAL_TABLE);

  // Edit modal state
  const [open, setOpen] = React.useState(false);
  const [editing, setEditing] = React.useState<ServiceRow | null>(null);

  /* ---------- ADD form (with 3rd generic to satisfy RHF v7.53+) ---------- */
  const addForm = useForm<AddValues, any, AddValues>({
    resolver: zodResolver(addSchema),
    defaultValues: {
      title: "",
      category: "Haircuts",
      durationMin: 45,
      price: 50,
      image: "",
    },
  });
  const imagePreview = addForm.watch("image");

  function onAdd(values: AddValues) {
    const id = crypto.randomUUID();
    const newRow: ServiceRow = {
      id,
      title: values.title.trim(),
      cat: values.category,
      dur: `${values.durationMin} min`,
      price: `$${Number(values.price).toFixed(2)}`,
      image: values.image || undefined,
    };
    setRows((prev) => [newRow, ...prev]);
    // demo logic: auto-feature "Haircuts"
    if (values.category === "Haircuts")
      setFeatured((prev) => [newRow, ...prev]);
    addForm.reset();
  }

  /* ---------- EDIT form (modal) ---------- */
  const form = useForm<EditValues, any, EditValues>({
    resolver: zodResolver(editSchema),
    defaultValues: {
      title: "",
      category: "Haircuts",
      durationMin: 45,
      price: 50,
      image: "",
    },
  });

  React.useEffect(() => {
    if (!editing) return;
    const minutes = parseInt(editing.dur.replace(/\D/g, ""), 10) || 0;
    const priceNumber = Number(editing.price.replace(/[^0-9.]/g, "")) || 0;
    form.reset({
      title: editing.title,
      category: (CATEGORIES.includes(editing.cat as any)
        ? editing.cat
        : "Haircuts") as EditValues["category"],
      durationMin: minutes,
      price: priceNumber,
      image: editing.image ?? "",
    });
  }, [editing, form]);

  function openEditModal(service: ServiceRow) {
    setEditing(service);
    setOpen(true);
  }

  function deleteService(service: ServiceRow) {
    setFeatured((prev) => prev.filter((s) => s.id !== service.id));
    setRows((prev) => prev.filter((s) => s.id !== service.id));
  }

  function upsertService(updated: ServiceRow) {
    setFeatured((prev) => {
      const i = prev.findIndex((s) => s.id === updated.id);
      if (i === -1) return prev;
      const copy = prev.slice();
      copy[i] = updated;
      return copy;
    });
    setRows((prev) => {
      const i = prev.findIndex((s) => s.id === updated.id);
      if (i === -1) return prev;
      const copy = prev.slice();
      copy[i] = updated;
      return copy;
    });
  }

  function onEditSubmit(values: EditValues) {
    if (!editing) return;
    const updated: ServiceRow = {
      ...editing,
      title: values.title.trim(),
      cat: values.category,
      dur: `${values.durationMin} min`,
      price: `$${Number(values.price).toFixed(2)}`,
      image: values.image || undefined,
    };
    // TODO: PATCH /api/services/:id
    upsertService(updated);
    setOpen(false);
    setEditing(null);
  }

  const filteredRows = rows.filter((r) => {
    const matchCat = category === "All" || r.cat === category;
    const q = search.trim().toLowerCase();
    const matchQ =
      !q ||
      r.title.toLowerCase().includes(q) ||
      r.cat.toLowerCase().includes(q) ||
      r.dur.toLowerCase().includes(q) ||
      r.price.toLowerCase().includes(q);
    return matchCat && matchQ;
  });

  return (
    <div className="bg-background-light dark:bg-background-dark font-display min-h-screen">
      <main className="p-6 lg:p-10">
        <div className="max-w-7xl mx-auto">
          {/* Heading */}
          <div className="flex flex-wrap justify-between items-start gap-4 mb-6">
            <div className="flex flex-col gap-1">
              <h1 className="text-text-light dark:text-text-dark text-3xl lg:text-4xl font-black tracking-[-0.033em]">
                Manage Services
              </h1>
              <p className="text-subtext-light dark:text-subtext-dark text-base">
                Add, edit, and manage the services your salon offers.
              </p>
            </div>
          </div>

          {/* Category slider */}
          <LocalHSlider ariaLabel="Service categories" className="mb-8">
            {CATEGORIES.map((c) => (
              <CategoryPill
                key={c}
                label={c}
                active={c === category}
                onClick={() => setCategory(c)}
              />
            ))}
          </LocalHSlider>

          {/* Grid */}
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Add Service */}
            <div className="lg:col-span-1">
              <div className="bg-content-light dark:bg-content-dark p-6 rounded-xl border border-border-light dark:border-border-dark">
                <h2 className="text-text-light dark:text-text-dark text-xl font-bold mb-6">
                  Add a New Service
                </h2>

                <Form {...addForm}>
                  <form
                    className="flex flex-col gap-5"
                    onSubmit={addForm.handleSubmit(onAdd)}
                  >
                    <FormField
                      control={addForm.control}
                      name="title"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Service Name</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="e.g., Men's Haircut"
                              className="h-12"
                              {...field}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                      <FormField
                        control={addForm.control}
                        name="durationMin"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel>Duration (min)</FormLabel>
                            <FormControl>
                              <Input
                                type="number"
                                min={1}
                                max={1440}
                                className="h-12"
                                {...field}
                              />
                            </FormControl>
                            <FormMessage />
                          </FormItem>
                        )}
                      />
                      <FormField
                        control={addForm.control}
                        name="price"
                        render={({ field }) => (
                          <FormItem>
                            <FormLabel>Price ($)</FormLabel>
                            <FormControl>
                              <Input
                                type="number"
                                step="0.01"
                                min={0.01}
                                className="h-12"
                                {...field}
                              />
                            </FormControl>
                            <FormMessage />
                          </FormItem>
                        )}
                      />
                    </div>

                    <FormField
                      control={addForm.control}
                      name="category"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Category</FormLabel>
                          <Select
                            value={field.value}
                            onValueChange={field.onChange}
                          >
                            <FormControl>
                              <SelectTrigger className="h-12">
                                <SelectValue placeholder="Select a category" />
                              </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                              {CATEGORIES.filter((c) => c !== "All").map(
                                (c) => (
                                  <SelectItem key={c} value={c}>
                                    {c}
                                  </SelectItem>
                                )
                              )}
                            </SelectContent>
                          </Select>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                      control={addForm.control}
                      name="image"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Image URL (optional)</FormLabel>
                          <FormControl>
                            <Input
                              placeholder="https://…"
                              className="h-12"
                              {...field}
                            />
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    {/* Live Preview */}
                    {imagePreview ? (
                      <div className="rounded-lg overflow-hidden border border-border-light dark:border-border-dark">
                        <div
                          className="aspect-video bg-cover bg-center"
                          style={{ backgroundImage: `url(${imagePreview})` }}
                        />
                      </div>
                    ) : null}

                    <div className="flex items-center gap-3 pt-2">
                      <Button
                        type="submit"
                        className="flex-1 h-10 bg-primary text-text-light"
                      >
                        Add Service
                      </Button>
                      <Button
                        type="button"
                        variant="secondary"
                        className="flex-1 h-10"
                        onClick={() => addForm.reset()}
                      >
                        Clear
                      </Button>
                    </div>
                  </form>
                </Form>
              </div>
            </div>

            {/* Right column */}
            <div className="lg:col-span-2">
              <div className="bg-content-light dark:bg-content-dark p-6 rounded-xl border border-border-light dark:border-border-dark">
                {/* Toolbar */}
                <div className="flex flex-col sm:flex-row gap-4 mb-6">
                  <div className="relative flex-grow">
                    <Input
                      placeholder="Search for a service..."
                      value={search}
                      onChange={(e) => setSearch(e.target.value)}
                      className="h-12 pl-3"
                    />
                  </div>
                  <div className="relative w-full sm:w-48">
                    <Select value={category} onValueChange={setCategory}>
                      <SelectTrigger className="h-12">
                        <SelectValue placeholder="All Categories" />
                      </SelectTrigger>
                      <SelectContent>
                        {CATEGORIES.map((c) => (
                          <SelectItem key={c} value={c}>
                            {c}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                {/* Featured slider */}
                <LocalHSlider ariaLabel="Featured services" className="mb-6">
                  {featured
                    .filter((f) => category === "All" || f.cat === category)
                    .map((f) => (
                      <FeaturedCard
                        key={f.id}
                        item={f}
                        onEdit={() => openEditModal(f)}
                        onDelete={() => deleteService(f)}
                      />
                    ))}
                </LocalHSlider>

                {/* Table */}
                <div className="overflow-x-auto">
                  <table className="w-full text-left">
                    <thead className="border-b border-border-light dark:border-border-dark">
                      <tr>
                        <th className="p-3 text-sm font-semibold text-subtext-light dark:text-subtext-dark">
                          Service
                        </th>
                        <th className="p-3 text-sm font-semibold text-subtext-light dark:text-subtext-dark hidden sm:table-cell">
                          Category
                        </th>
                        <th className="p-3 text-sm font-semibold text-subtext-light dark:text-subtext-dark hidden md:table-cell">
                          Duration
                        </th>
                        <th className="p-3 text-sm font-semibold text-subtext-light dark:text-subtext-dark hidden md:table-cell">
                          Image
                        </th>
                        <th className="p-3 text-sm font-semibold text-subtext-light dark:text-subtext-dark">
                          Price
                        </th>
                        <th className="p-3 text-sm font-semibold text-subtext-light dark:text-subtext-dark text-right">
                          Actions
                        </th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-border-light dark:divide-border-dark">
                      {filteredRows.map((r) => (
                        <tr
                          key={r.id}
                          className="hover:bg-primary/5 dark:hover:bg-primary/10"
                        >
                          <td className="p-3 text-sm font-medium text-text-light dark:text-text-dark">
                            {r.title}
                          </td>
                          <td className="p-3 text-sm text-subtext-light dark:text-subtext-dark hidden sm:table-cell">
                            {r.cat}
                          </td>
                          <td className="p-3 text-sm text-subtext-light dark:text-subtext-dark hidden md:table-cell">
                            {r.dur}
                          </td>
                          <td className="p-3 hidden md:table-cell">
                            {r.image ? (
                              <div
                                className="h-10 w-16 rounded-md bg-cover bg-center border border-border-light dark:border-border-dark"
                                style={{ backgroundImage: `url(${r.image})` }}
                              />
                            ) : (
                              <span className="text-xs text-subtext-light dark:text-subtext-dark">
                                —
                              </span>
                            )}
                          </td>
                          <td className="p-3 text-sm font-medium text-text-light dark:text-text-dark">
                            {r.price}
                          </td>
                          <td className="p-3">
                            <div className="flex justify-end gap-2">
                              <button
                                className="p-2 rounded-md hover:bg-primary/20"
                                onClick={() => openEditModal(r)}
                              >
                                <span className="material-symbols-outlined text-base text-subtext-light dark:text-subtext-dark">
                                  edit
                                </span>
                              </button>
                              <button
                                className="p-2 rounded-md hover:bg-red-500/20"
                                onClick={() => deleteService(r)}
                              >
                                <span className="material-symbols-outlined text-base text-red-500">
                                  delete
                                </span>
                              </button>
                            </div>
                          </td>
                        </tr>
                      ))}
                      {filteredRows.length === 0 && (
                        <tr>
                          <td
                            colSpan={6}
                            className="p-6 text-center text-sm text-subtext-light dark:text-subtext-dark"
                          >
                            No services match your filters.
                          </td>
                        </tr>
                      )}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
            {/* end right */}
          </div>
        </div>
      </main>

      {/* Edit Service Modal */}
      <Dialog
        open={open}
        onOpenChange={(v) =>
          v ? setOpen(true) : (setOpen(false), setEditing(null))
        }
      >
        <DialogContent className="sm:max-w-[560px]">
          <DialogHeader>
            <DialogTitle>Edit service</DialogTitle>
            <DialogDescription>
              Update the service details and save your changes.
            </DialogDescription>
          </DialogHeader>

          <Form {...form}>
            <form
              onSubmit={form.handleSubmit(onEditSubmit)}
              className="space-y-4"
            >
              <FormField
                control={form.control}
                name="title"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Service name</FormLabel>
                    <FormControl>
                      <Input placeholder="e.g., Women's Haircut" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="category"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Category</FormLabel>
                      <Select
                        value={field.value}
                        onValueChange={field.onChange}
                      >
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="Select a category" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          {CATEGORIES.filter((c) => c !== "All").map((c) => (
                            <SelectItem key={c} value={c}>
                              {c}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="durationMin"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Duration (minutes)</FormLabel>
                      <FormControl>
                        <Input type="number" min={1} max={1440} {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="price"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Price (USD)</FormLabel>
                      <FormControl>
                        <Input
                          type="number"
                          step="0.01"
                          min={0.01}
                          {...field}
                        />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="image"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Image URL (optional)</FormLabel>
                      <FormControl>
                        <Input placeholder="https://…" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>

              {/* Preview */}
              {form.watch("image") ? (
                <div className="rounded-lg overflow-hidden border border-border-light dark:border-border-dark">
                  <div
                    className="aspect-video bg-cover bg-center"
                    style={{ backgroundImage: `url(${form.watch("image")})` }}
                  />
                </div>
              ) : null}

              <DialogFooter className="gap-2">
                <Button
                  type="button"
                  variant="secondary"
                  onClick={() => setOpen(false)}
                >
                  Cancel
                </Button>
                <Button type="submit">Save changes</Button>
              </DialogFooter>
            </form>
          </Form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
